<script>
	import { timer, elapsed } from "./clock.js";  
	import { range } from 'd3';
	import { scaleLinear,scaleLog } from 'd3-scale';
	let run=false
	let m = { x: 0, y: 0 };
	let inst="none"
	let w="none"
	let N=20
	let p={
		wavg:undefined,
		usum:undefined,
		wspread:undefined,
		w:[]
	}
let ws = scaleLinear()
  .domain([0, 4.0]) 
  .range([100, 800]) 
	
	
	function handleMousemove(event) {
		const rect = event.currentTarget.getBoundingClientRect()
		m.x = event.clientX-rect.x;
		m.y = event.clientY-rect.y;
	}
	
$: if (m.y>40 && m.y<207){w="equal"; p.wspread=0} 
		else if (m.y>233 && m.y<399){w="average"; p.wspread=0.5}  
		else if (m.y>424 && m.y<592){w="inequality"; p.wspread=p.wavg}
		else {w=""}
	
$: if (m.x>110 && m.x<270){inst="OA"} 
		else if (m.x>293 && m.x<453){inst="PfHw"}  
		else if (m.x>476 && m.x<636){inst="PfLw"}
		else if (m.x>659 && m.x<819){inst="EC"}
		else if (m.x>842 && m.x<1002){inst="TQ"}
		else if (m.x>1025 && m.x<1185){inst="PA"}
		else {inst=""}
	
	$: if (w=="equal"){p.wavg=(207-m.y)/(207-40)*1.5}
		else if (w=="average"){p.wavg=(399-m.y)/(399-233)*1.5}
		else if (w=="inequality"){p.wavg=(592-m.y)/(592-424)*1.5}
		//else {wavg=undefined}
	
		$: if (inst=="OA"){p.usum=(m.x-110)/(270-110)*5}
		else if (inst=="PfHw"){p.usum=(m.x-293)/(453-293)*5}
		else if (inst=="PfLw"){p.usum=(m.x-476)/(636-476)*5}
	else if (inst=="EC"){p.usum=(m.x-659)/(819-659)*5}
	else if (inst=="TQ"){p.usum=(m.x-842)/(1002-842)*5}
	else if (inst=="PA"){p.usum=(m.x-1025)/(1185-1025)*5}
	
	$: if (w=="equal") {p.w=Array(N).fill(p.wavg)} else if (w=="average") {p.w=  range(p.wavg-p.wspread,p.wavg+p.wspread,(2*p.wspread)/N).map(x => Math.max(0.0,x));} else if (w=="inequality") {p.w=  range(0,p.wavg+p.wspread,(p.wavg+p.wspread)/N)}
	
	$: run ? timer.start() : timer.stop();
	
	$: {elapsed;
		 
		 }
	
</script>

<div class="container" on:mousemove={handleMousemove}>

</div>
<div class="sim">
	<div style="width:200px">
		<p>

			Institution: {inst}
		</p>
		wage spread: {w}
		<p>
			wavg: {p.wavg ? p.wavg.toFixed(2) : p.wavg}
		</p>
		<p>
			usum: {p.usum ? p.usum.toFixed(2) : p.usum}
		</p>
		<p>
			wspread: {p.wspread ? p.wspread.toFixed(2) : p.wspread}
		</p>
		{$elapsed}
	</div>
	<div>
		<svg width="1200px" height="500px" viewBox="0 0 400 400" xmlns="http://www.w3.org/2000/svg">
			{#each p.w as v}
			<circle cx={ws(v)} cy="154" r="3" />
			{/each}
		</svg>
	</div>
</div>

