	.container {
		position: relative;
		padding:0px;
		margin:0px;
		width: 1200px; 
		height: 650px; 
		background-image: url("https://zahachtah.github.io/CAS/images/testfig.png")}
	.OA{
		position: absolute;
		left: 110px;
		top: 39px;
		width: 159px;
		height:553px;
		background-color:#dddd
	}
		.PfH{
		position: absolute;
		left: 293px;
		top: 39px;
		width: 159px;
		height:553px;
		background-color:#dddd
	}
	.sim{
		display:flex;
		flex-direction:row
	}

</style>